html *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

.login-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-container>div{
    background-color: whitesmoke;
    padding: 5em;
    border-radius: 10px;   
}

.login-container form{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-container button{
    padding: 5px;
}

.login-response{
    text-align: center;
    margin-top: 20px;
}


/* Input */
.input-container{
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
}

.input-container label{
    font-size: 25px;
}

.input-container input{
    width: 250px;
    padding: 3px 5px;
    font-size: 18px;
}

.input-container .error{
    font-size: 14px;
    color: red;
}


.layout{
    background-color: #D8D5D1;
    min-height: 100vh;
    width: 100vw;
    display: flex;
}

.navigation{
    display: flex;
    flex-direction: column;
    background-color: #172E38;
    padding: 3em 1em;
    min-width: 220px;
}

.navigation div{
    margin-bottom: 15px;
    display: flex;
    align-items: center;
}

.navigation a{
    color: whitesmoke;
    text-decoration: none;
}

.navigation svg{
    color: whitesmoke;
    margin-right: 5px;
    font-size: 18px;
}

.main{
    display: flex;
    flex-direction: column;
    flex: 1;
}

.header{
    background-color: #cbcac6;
    padding: 1em;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
}

.container{
    padding: 1em;
}

.actual-client{
    display: flex;
}

.actual-client select{
    margin-right: 30px;
}

#actual{
    font-weight: bold;
}

.configurations{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.results-table,
.log-table{
    background-color: white;
    width: 60%;
    border-collapse: collapse;
}

.log-table{
    width: 100%;
}

.results-table th,
.log-table th{
    text-align: left;
    padding: 10px 5px;
    border-bottom: 1px solid black;
}

.results-table td,
.log-table td{
    padding:5px;
}

.results-table input{
    margin-left: 5em;
}

button.save{
    margin-top: 10px;
    padding: 5px 10px;
    background-color: #172E38;
    border: none;
    color: white;
    font-size: 20px;
    border-radius: 3px;
    cursor: pointer;
}

.documents{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.client-container{
    /* display: flex; */
    background-color: white;
    padding: 1em;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.check{
    margin-top: 10px;
}

.check label{
    margin-right: 5px;
}

.w-100{
    width: 100%;
    margin-top: 2em;
    display: flex;
    justify-content: space-between;
}

.w-100 input{
    padding: 5px;
}